import ToggleField from './fields/ToggleField';
import ToggleOptionsField from './fields/ToggleOptionsField';
import SelectField from './fields/SelectField';
import TextField from './fields/TextField';
import NumberField from './fields/NumberField';
import RadioField from './fields/RadioField';
import TextareaField from './fields/TextareaField';
import ButtonField from './fields/ButtonField';
import RadioGroupField from './fields/RadioGroupField';
import RangeSelect from './fields/RangeSelect';
import ImageField from './fields/ImageSelectField';

const FieldRenderer = ( { field, fieldState, handleChange } ) => {
	switch ( field.type ) {
		case 'toggle':
			return (
				<ToggleField
					fieldState={ fieldState }
					field={ field }
					handleChange={ ( val ) => handleChange( fieldState, field.name, val ) }
				/>
			);
		case 'options_toggle':
			return (
				<ToggleOptionsField
					fieldState={ fieldState }
					field={ field }
					handleChange={ ( val ) => handleChange( fieldState, field.name, val ) }
					trueValue={ field.trueValue }
					falseValue={ field.falseValue }
				/>
			);
		case 'select':
			return (
				<SelectField
					fieldState={ fieldState }
					field={ field }
					handleChange={ ( val ) => handleChange( fieldState, field.name, val ) }
				/>
			);
		case 'text':
			return (
				<TextField
					fieldState={ fieldState }
					field={ field }
					handleChange={ ( val ) => handleChange( fieldState, field.name, val ) }
				/>
			);
		case 'number':
			return (
				<NumberField
					fieldState={ fieldState }
					field={ field }
					handleChange={ ( val ) => handleChange( fieldState, field.name, val ) }
				/>
			);
		case 'radio':
			return (
				<RadioField
					fieldState={ fieldState }
					field={ field }
					handleChange={ ( val ) => handleChange( fieldState, field.name, val ) }
				/>
			);
		case 'textarea':
			return (
				<TextareaField
					fieldState={ fieldState }
					field={ field }
					handleChange={ ( val ) => handleChange( fieldState, field.name, val ) }
				/>
			);
		case 'ia_radio':
			return (
				<RadioGroupField
					fieldState={ fieldState }
					field={ field }
					handleChange={ ( val ) => handleChange( fieldState, field.name, val ) }
				/>
			);
		case 'range_select':
			return (
				<RangeSelect
					fieldState={ fieldState }
					field={ field }
					handleChange={ ( val ) => handleChange( fieldState, field.name, val ) }
				/>
			);
		case 'image_select':
			return (
				<ImageField
					fieldState={ fieldState }
					field={ field }
					handleChange={ ( val ) => handleChange( fieldState, field.name, val ) }
				/>
			);

		case 'button':
			return <ButtonField field={ field } />;
		default:
			return null; // dacă nu există tip valid
	}
};

export default FieldRenderer;
